import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Card from "../components/Card"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { Form, Button, InputGroup, Breadcrumb, Table } from '@themesberg/react-bootstrap';
import Grid from '../components/Grid'
import { navigate } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell, faHome, faEnvelopeOpen, faSearch, faSignOutAlt, faUserShield, faAngleDown, faEye } from "@fortawesome/free-solid-svg-icons";
import Parse from '../vendor/Parse'
import { useLocation } from '@reach/router';

const { useState } = React;

const Profile1 = "images/2021-01-30.jpg";
const IndexPage = () => {

  const location = useLocation();
  const qs = new URLSearchParams(location.search)

  const [items, setItems] = useState([])
  const [query, setQuery] = useState(qs.get('search') || "")
  const searchNumbers = async (query) => {
    const res = await Parse.Cloud.run('queryNumbers', {
      query
    })
    setItems(res.data)
  }

  React.useEffect(() => {
    searchNumbers(query)
  }, [])

  const handleSearchButton = async () => {
    await searchNumbers(query)
  }

  return <Layout afterSearch={<></>} >
    <h3>SG Mobile Number Finding</h3>
    <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4 flex-column">
      <Form className="">
        <Form.Group id="topbarSearch">
          <InputGroup className="input-group-merge search-bar">
            <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
            <Form.Control type="text" name="search" placeholder="Search" value={query} onChange={(e) => setQuery(e.target.value)} />
            <Button type="submit" onClick={handleSearchButton} variant="primary" size="sm" className="me-2">
              Find
            </Button>
          </InputGroup>
        </Form.Group>
      </Form>
      <Table>
        <thead className="thead-light">
          <tr>
            <th className="border-0 align-middle">Number</th>
            <th className="border-0  align-middle">Telco</th>
            <th className="border-0 align-middle">Last Seen</th>
            <th className="border-0 align-middle">See</th>
          </tr>
        </thead>
        <tbody>
          {
            items.map(e => <tr>
              <td className="border-0  align-middle">
                <a href="">{e.num}</a>
              </td>
              <td className="border-0 fw-bold align-middle"> {e?.source}</td>
              <td className="border-0 text-danger align-middle">
                {e?.date}
              </td>
              <td className="border-0 text-danger align-middle">
                <a href={`https://api.whatsapp.com/send?phone=6587413159&text=` + encodeURIComponent(`Enquiry on ${e.num} with id ${e._id}`)}>
                  <Button>
                    <FontAwesomeIcon icon={faEye} />
                  </Button>
                </a>
              </td>

            </tr>)
          }
          {
            !items.length && <tr>
              <td colSpan={3} className="border-0">Key in 3 to 5 Numbers to start finding</td>

            </tr>
          }
        </tbody>
      </Table>
    </div>
  </Layout >
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
